<template lang="pug">
	v-app
		v-app-bar(
			app
			max-height='1000'
			flat
		)
			v-app-bar-nav-icon
			
		v-content
			v-container
				//- Current wallpaper preview
				v-row.mb-12.align-center
					v-col(cols='5')
						v-card.round(elevation='22')
							v-skeleton-loader(type='image' max-height='350' :value='!!currentWallpaper')
								v-img.round(v-if='currentWallpaper' :src='currentWallpaper.baseUrl' max-height='350')

					//- Current item details
					v-col.pb-10(cols='7')
						.pa-5
							h3.headline.font-weight-bold Current wallpaper
							v-breadcrumbs.px-1.pt-1.pb-2(:items='breadcrumbs')
								template(v-slot:divider)
									v-icon mdi-chevron-right
							v-btn.ma-1(outlined) Previous
							v-btn.ma-1(outlined) Next
							v-btn.ma-1(outlined) Random

				//- Album list
				v-skeleton-loader(
					type='list-item@10'
					:loading='!(favorites.length && albums.length)'
				)
					h3.subtitle-1.font-weight-bold.ma-3 Selected albums

					v-expansion-panels.albums(accordion multiple)
						//- Favorites panel
						v-expansion-panel
							v-expansion-panel-header.py-0(v-slot='open')
								v-layout
									v-checkbox(
										label='Favorites'
										:value='favorites'
										@click.native='preventExpansion'
									)
							v-expansion-panel-content
								media-items(:media-items='favorites' source='Favorites')
								

						//- Album panels list           
						v-expansion-panel(
							v-for='album in albums'
							:key='album.id'
							@change='loadAlbum(album.id)'
						)
							v-expansion-panel-header.py-0(v-slot='open')
								v-layout
									v-checkbox(
										v-model='selectedAlbums'
										:label='album.title'
										:value='album.id'
										@click.native='preventExpansion'
									)
							v-expansion-panel-content
								v-skeleton-loader(type='image' :loading='!album.mediaItems')
									media-items(:media-items='album.mediaItems' :source='album.title')
</template>

<script>
import MediaItems from '@/components/MediaItems.vue'

export default {
	name: 'App',

	components: {
		MediaItems
	},

	mounted() {
		window.eel.get_current_wallpaper()(mediaItem => {
			console.log('item', mediaItem)
			this.$data.currentWallpaper = mediaItem;
		});

		window.eel.get_favorites()(({ mediaItems }) => {
			this.$data.favorites = mediaItems;
		});

		window.eel.get_albums()(({ albums }) => {
			this.$data.albums= albums;
		});
	},

	methods: {
		preventExpansion: function(event) {
			// Disable click bubbling below an element on click event
			event.cancelBubble = true;
		},

		loadAlbum: function(albumId) {

			// TODO: Load next page of photos on scroll

			window.eel.get_album_media_items(albumId)(({ mediaItems }) => {
				this.albums = this.albums.map(album => {
					if (album.id == albumId) {
						album.mediaItems = mediaItems;
					}
					return album;
				})
			})
		},

		
	},

	computed: {
		breadcrumbs: function() {
			return [
				{ text: this.currentWallpaper.source },
				{ text: this.currentWallpaper.filename }
			]
		}
	},

	data: () => ({
		selectedAlbums: [],
		currentWallpaper: {},
		favorites: [],
		albums: []
	}),
};
</script>

<style>
	.round {
		border-radius: 10px !important;
	}
</style>